.project-alt-div3 div{
    overflow:hidden;
  }
.project-alt-div3 .img-div{
    overflow:hidden;
    float:right;
    height:82%;
}

.project-alt-div3 .side-by-side{
  display: grid;
  height: 40vh;
  padding:auto;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.project-alt-div3 .side-by-side div{
  align-items: center;
  justify-content: center;
}
.project-alt-div3 .side-by-side h1{
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size:10vh;
    font-weight: bold;
    letter-spacing: 1;
    color: white;
    padding:0;
    margin:0;
    text-shadow:    1px 1px 1px black,  
                2px 2px 0px #eb452b, 
                3px 3px 0px #efa032, 
                4px 4px 0px #46b59b, 
                5px 5px 0px #017e7f, 
                6px 6px 0px #052939/*, 
                7px 7px 0px #c11a2b, 
                8px 8px 0px #c11a2b*/;
}