  .date__box {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #ccc;
    border: 4px solid;
    font-weight: bold;
    padding: 5px 10px;
  }
  .date__box .date__month {
    font-size: 22px;
  }
  .project-card {
    padding: 30px;
    position: relative;
  }
  .project-card .date__box {
    opacity: 0;
    transform: scale(0.5);
    transition: 300ms ease-in-out;
  }
  .project-card .project-card__background, .project-card .card__background--layer {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .card__background--main{
    background-image: url('https://i.pinimg.com/564x/c4/37/3f/c4373f780e7f9543dfd0d56666e47dfa.jpg');
  }
  .project-card .project-card__background {
    padding: 15px;
    background: white;
  }
  .project-card .card__background--wrapper {
    height: 100%;
    clip-path: polygon(0 0, 100% 0, 100% 80%, 0 60%);
    position: relative;
    overflow: hidden;
  }
  .project-card .card__background--main {
    height: 100%;
    position: relative;
    transition: 300ms ease-in-out;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .project-card .card__background--layer {
    z-index: 0;
    opacity: 0;
    background: rgba(51, 51, 51, 0.9);
    transition: 300ms ease-in-out;
  }
  .project-card .project-card__head {
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .project-card .project-card__info {
    z-index: 10;
    position: relative;
    background: white;
    padding: 20px 15px;
  }
  .project-card .project-card__info h5 {
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: cal(5vh + 2vw);
  }
  .project-card .project-card__info h5 {
    transition: 300ms ease-in-out;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  }
  .project-card:hover .date__box {
    opacity: 1;
    transform: scale(1);
  }
  .project-card:hover .card__background--main {
    transform: scale(1.2) rotate(5deg);
  }
  .project-card:hover .card__background--layer {
    opacity: 1;
  }
  .project-card:hover .project-card__info h5 {
    color: #ffb535;
  }