.project-alt-div2 div{
    display: inline-block;
    align-items: center;
    margin-right:0;
    overflow:hidden;
}
.project-alt-div2 > div:nth-of-type(2){
    display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.project-alt-div2 h1{
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size:calc( 2vh + 5vw);
    letter-spacing: 2px;
    color: white;
    padding:0;
    margin:0;
    text-shadow:    1px 1px 1px black,  
                2px 2px 0px #eb452b, 
                3px 3px 0px #efa032, 
                4px 4px 0px #46b59b, 
                5px 5px 0px #017e7f, 
                6px 6px 0px #052939/*, 
                7px 7px 0px #c11a2b, 
                8px 8px 0px #c11a2b*/;
}

.project-alt-div2 h2{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    letter-spacing: 1;
}