.dropdown-menu {
  z-index:12;
  background: #ff0000;
  width: 175px;
  position: absolute;
  top: 70px;
  list-style: none;
  text-align: start;
}

.dropdown-menu li {
  background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);;
  cursor: pointer;
}

.dropdown-menu li:hover {
  background: #2f3241;
}

.dropdown-menu-clicked {
  display: block;
}

.dropdown-link {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: #fff;
  padding: 16px;
}

@media screen and (max-width: 960px) {
  .fa-caret-down {
    display: inline-block;
  }
}
