.project-alt .project-header-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #2f3241;
  background-size: cover;
  box-shadow: inset 0 0 10em rgba(0, 0, 0, 0.75);
  margin: 0;
  height: 70vh;
}
.project-gif-div {
  height: 70vh;
}

.project-alt-parent {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(7, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  margin: 1vw;
}

.project-alt-parent div {
  margin: 1vw;
  height: 80vh;
  border-radius: 10px;
  background-size: cover;
}

.project-alt-div1 {
  grid-area: 1 / 1 / 2 / 5;
  border-radius: 20%;
  background: url("https://c4.wallpaperflare.com/wallpaper/268/726/529/stroke-artwork-abstract-coloured-lines-wallpaper-preview.jpg");
 color: white;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.project-alt-div2 {
  grid-area: 2 / 1 / 3 / 5;
  background: url("https://c4.wallpaperflare.com/wallpaper/232/769/83/3d-architecture-depth-of-field-wallpaper-preview.jpg");
  box-shadow: inset 0 0 10em rgba(0, 0, 0, 0.75);
  color: white;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.project-alt-div3 {
  grid-area: 3 / 1 / 4 / 5;
  background-color: #082032;
  color: white;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.project-alt-div4 {
  grid-area: 4 / 1 / 5 / 3;

  background-color: green;
}
.project-alt-div5 {
  grid-area: 4 / 3 / 5 / 5;

  background-color: yellow;
}
.project-alt-div6 {
  grid-area: 5 / 1 / 6 / 2;

  background-color: red;
}
.project-alt-div7 {
  grid-area: 5 / 2 / 6 / 5;

  background-color: plum;
}
.project-alt-div8 {
  grid-area: 6 / 1 / 8 / 3;
  background-color: grey;
  height: 140vh;
}
.project-alt-div9 {
  grid-area: 6 / 3 / 7 / 5;

  background-color: lemonchiffon;
}
.project-alt-div10 {
  grid-area: 7 / 1 / 8 / 5;
  background: url("https://c4.wallpaperflare.com/wallpaper/286/114/401/light-background-wallpaper-preview.jpg")
    no-repeat;
  background-size: cover;
}

.close-modal {
  display: flex;
  float: right;
  margin: 1vw;
  border: none;
  height: 30px;
  z-index: 14;
  position: relative;
}

/*--------------------------------Code for metallic Text--------------------------------*/
@keyframes shine {
  0% {
    background-position: 110% 0%, 0 0;
  }
  100% {
    background-position: -10% 0%, 0 0;
  }
}
.project-alt .project-header-div h2 {
  position: relative;
  display: inline-block;
  font-family: "Source Sans Pro", sans-serif, Haettenschweiler;
  font-weight: 800;
  font-size: 5vw;
  letter-spacing: 2vw;
  text-transform: uppercase;
  margin: 0;
}
[data-gold] {
  color: #ac733c;
}
@supports ((background-clip: text) or (-webkit-background-clip: text)) {
  [data-gold] {
    color: transparent;
    background-image: linear-gradient(
        to right,
        transparent 0%,
        transparent 45%,
        white 50%,
        transparent 55%,
        transparent 100%
      ),
      linear-gradient(
        90deg,
        #c78c48,
        #cf9147 9.4%,
        #cf9348 9.4%,
        #b2763e 33.6%,
        #ad743c 35.7%,
        #ac723d 46.9%,
        #b0773d 51.7%,
        #b0793e 52.1%,
        #c4914c 64.6%,
        #c99750 68.5%,
        #ce9e54 73.2%,
        #f4d188
      );
    background-position: 110% 0%, 0 0;
    background-size: 200% auto, auto;
    -webkit-background-clip: text;
    background-clip: text;
    animation: shine 2s ease-in-out 2 alternate 2s;
  }
}
[data-gold]:before {
  content: attr(data-gold);
  color: #f0d8a9;
  position: absolute;
  z-index: -1;
  top: -0.08vw;
  left: 0px;
  text-shadow: black 0px 0.08vw 12px;
}
@supports ((background-clip: text) or (-webkit-background-clip: text)) {
  [data-gold]:after {
    content: attr(data-gold);
    position: absolute;
    top: 0;
    left: 0;
    background-image: linear-gradient(
      to bottom,
      transparent 0%,
      transparent 48%,
      rgba(98, 16, 0, 0.5) 50%,
      transparent 75%
    );
    -webkit-background-clip: text;
    background-clip: text;
  }
}
[data-silver] {
  color: #858585;
}
@supports ((background-clip: text) or (-webkit-background-clip: text)) {
  [data-silver] {
    color: transparent;
    background-image: linear-gradient(
        to right,
        transparent 0%,
        transparent 45%,
        white 50%,
        transparent 55%,
        transparent 100%
      ),
      linear-gradient(
        270deg,
        #8c8c8c 1.3%,
        #999 15%,
        #868686 29.6%,
        #828282 29.6%,
        #7d7d7d 31.8%,
        #797979 31.8%,
        #6a6a6a 38.9%,
        #d3d3d3
      );
    background-position: 110% 0%, 0 0;
    background-size: 200% auto, auto;
    -webkit-background-clip: text;
    background-clip: text;
    animation: shine 2s ease-in-out 2 alternate-reverse 2s;
  }
}
[data-silver]:before {
  content: attr(data-silver);
  color: #fff;
  position: absolute;
  z-index: -1;
  top: -0.3vw;
  left: 0px;
  text-shadow: black 0px 0.08vw 12px;
}
@supports ((background-clip: text) or (-webkit-background-clip: text)) {
  [data-silver]:after {
    content: attr(data-silver);
    position: absolute;
    top: 0;
    left: 0;
    background-image: linear-gradient(
      to bottom,
      transparent 0%,
      transparent 48%,
      rgba(17, 17, 17, 0.5) 50%,
      transparent 75%
    );
    -webkit-background-clip: text;
    background-clip: text;

