}

/*--------------------------------Modal Open Button--------------------------------*/
.open-modal {
  display: flex;
  height: 5vh;
  width:fit-content;
  font-size: 2vh;
  font-weight: 200;
  letter-spacing: 1px;
  outline: 0;
  border: 1px solid whitesmoke;
  border-radius: 0;
  cursor: pointer;
  position: relative;
  background-color: rgba(0, 0, 0, 0);
  z-index:2;
}

.open-modal i {
  opacity: 0;
  font-size: 13px;
  transition: 0.2s;
  float: right;
  padding-top: 1px;
  transition: transform 1;
}

.open-modal div {
  transition: transform 0.8s;
}

.open-modal:hover div {
  transform: translateX(-8px);
}

.open-modal::after {
  content: "";
  background-color: #66f2d5;
  width: 100%;
  z-index: -1;
  position: absolute;
  height: 100%;
  top: 7px;
  left: 7px;
}

.open-modal:hover i {
  opacity: 1;
  transform: translateX(-6px);
}
